import React from 'react'
import Container from 'Shared/components/Container/index'
import withTranslation from 'Shared/hoc/withTranslations/index'
import './Settings.scss'

const Settings = ({ t, children }) => (
  <section className='settings-view__container parsing--hidden'>
    <Container>
      <div className='settings__heading'>
        <h1 className='settings__header'>{t('common.settings')}</h1>
      </div>
      { children }
    </Container>
  </section>
)

export default withTranslation(Settings)



