import React from 'react'
import CandidateSettingsViewMobile from './SettingsView.mobile'
import CandidateSettingsView from './SettingsView'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const CandidateSettingsViewComponent = props => props.isMobile ? <CandidateSettingsViewMobile {...props} /> : <CandidateSettingsView {...props} />

export default withDeviceWidthDetector(CandidateSettingsViewComponent)



