import React, { Component } from 'react'
import { Field } from 'redux-form'
import { Input } from 'Shared/components/FormElements'
import withTranslations from 'Shared/hoc/withTranslations'
import { DELETE_CONFIRM_WORD } from 'Shared/constants/User'

import './DleteAccountForm.scss'

class DeleteAccountForm extends Component {
  render () {
    const { t, handleSubmit } = this.props
    return (
      <form onSubmit={handleSubmit}>
        <Field
          component={Input}
          iconClassName='ion-md-lock'
          label={t('deleteAccount.confirmation.form.label', { confirmWord: DELETE_CONFIRM_WORD })}
          placeholder=' '
          name='confirmText'
          autoComplete='off'
          type='text'
        />
      </form>
    )
  }
}

export default withTranslations(DeleteAccountForm)



