import React, { PureComponent } from 'react'
import CandidateNotificationsFormConnector from 'Candidate/connectors/Forms/Settings/CandidateNotificationsFormConnector'
import { Helmet } from 'react-helmet'
import { t } from 'i18next'

class Notifications extends PureComponent {
  render () {
    return (
      <div>
        <Helmet>
          <title>{t('meta.candidate.title.notifications')}</title>
          <meta property='og:title' content={t('meta.candidate.title.notifications')} />
        </Helmet>
        <CandidateNotificationsFormConnector />
      </div>
    )
  }
}

export default Notifications



