import { connect } from 'react-redux'
import { t } from 'i18next'
import { withRouter } from 'react-router-dom'
import ExportDataSection from 'Shared/components/ExportDataSection/ExportDataSection'
import UserService from 'Shared/services/UserService'
import NotificationService from 'Shared/services/NotificationService'

const userService = new UserService()

const mapStateToProps = () => ({
  exportData: (openSuccessModal) => async () => {
    try {
      await userService.exportCandidateData()
      return openSuccessModal()
    } catch (e) {
      console.error(e)
      return NotificationService.error(t('manageAccount.exportData.exportDataFailed'))
    }
  }
})

export default withRouter(connect(mapStateToProps)(ExportDataSection))



