import { DELETE_CONFIRM_WORD } from 'Shared/constants/User'

export default values => {
  const errors = {}

  if (values['confirmText'] !== DELETE_CONFIRM_WORD) {
    errors['confirmText'] = {
      key: 'errors.confirmDoesntMatch',
      options: {
        confirmWord: DELETE_CONFIRM_WORD
      }
    }
  }

  return errors
}



