import { reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { t } from 'i18next'
import RequestChangeEmailForm from 'Shared/forms/ChangeEmail/RequestChangeEmailForm'
import { validate, verifyNewEmail } from 'Shared/validators/changeEmail'
import NotificationService from 'Shared/services/NotificationService'
import UserService from 'Shared/services/UserService'

const userService = new UserService()

export const formName = 'CandidateRequestChangeEmailForm'

let Form = reduxForm({
  form: formName,
  validate,
  asyncValidate: verifyNewEmail(userService.verifyCandidateEmail)
})(RequestChangeEmailForm)

const mapStateToProps = state => ({
  userEmail: state.user.details.email
})

const mapDispatchToProps = dispatch => ({
  requestChangeEmail: async (values) => {
    try {
      await userService.requestChangeCandidateEmail(values.newEmail)
      return NotificationService.success(t('alerts.changeEmailSuccess'))
    } catch (err) {
      NotificationService.error(t('alerts.changeEmailFailed'))
      throw err
    }
  }
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Form))



