import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import withTranslations from 'Shared/hoc/withTranslations/index'
import Modal from 'Shared/components/Modal/index'
import { Trans } from 'react-i18next'
import './DeleteAccountSection.scss'

class DeleteAccountSection extends PureComponent {
  state = {
    isPreConfirmationModalOpen: false,
    isConfirmationModalOpen: false
  }

  componentDidUpdate (prevProps) {
    if (this.props.isDeleteAccountFormSubmitSucceeded &&
      this.props.isDeleteAccountFormSubmitSucceeded !== prevProps.isDeleteAccountFormSubmitSucceeded) {
      this.closeConfirmationModal()
    }
  }

  openPreConfirmationModal = () => this.setState({ isPreConfirmationModalOpen: true })
  closePreConfirmationModal = () => this.setState({ isPreConfirmationModalOpen: false })

  openConfirmationModal = () => this.setState({ isConfirmationModalOpen: true })
  closeConfirmationModal = () => this.setState({ isConfirmationModalOpen: false })

  onSubmitPreConfirmation = () => {
    this.closePreConfirmationModal()
    this.openConfirmationModal()
  }
  onSubmitDelete = () => this.props.submitDeleteAccountForm()

  render () {
    const { t, isDeleteAccountFormSubmitting, isDeleteAccountFormInvalid, getDeleteAccountConnector, userRole } = this.props

    return (
      <div>
        <div>
          <p className='delete-account__description'>
            {`${t(`deleteAccount.${userRole}.description`)} `}
            <button id='initDelete' onClick={this.openPreConfirmationModal} className='delete-account__button'>{t(`deleteAccount.${userRole}.button`)}</button>
          </p>
        </div>
        <Modal
          id='preConfirmationModal'
          title={t(`deleteAccount.${userRole}.preConfirmation.title`)}
          handleClose={this.closePreConfirmationModal}
          show={this.state.isPreConfirmationModalOpen}
          handleCancel={this.onSubmitPreConfirmation}
          handleSubmit={this.closePreConfirmationModal}
          cancelText={t(`deleteAccount.${userRole}.preConfirmation.submit`)}
          submitText={t(`deleteAccount.${userRole}.preConfirmation.cancel`)}
          showContact={false}
          cancelButtonClassName='delete-account__delete-button'
          submitButtonClassName='delete-account__cancel-button'
          footerClassName='delete-account__footer'
        >
          <Trans i18nKey={`deleteAccount.${userRole}.preConfirmation.description`} className='delete-account-form__description'>
            Are you sure you would like to delete your profile on Oliv? Once this is done, your profile and all your
            applications will be <strong>permanently</strong> removed from Oliv.
          </Trans>
        </Modal>
        <Modal
          title={t(`deleteAccount.${userRole}.confirmation.title`)}
          handleClose={this.closeConfirmationModal}
          show={this.state.isConfirmationModalOpen}
          handleCancel={this.onSubmitDelete}
          handleSubmit={this.closeConfirmationModal}
          cancelText={isDeleteAccountFormSubmitting ? `${t('common.loading')}...` : t(`deleteAccount.${userRole}.confirmation.submit`)}
          submitText={t(`deleteAccount.${userRole}.confirmation.cancel`)}
          showContact={false}
          disableCancelButton={isDeleteAccountFormInvalid || isDeleteAccountFormSubmitting}
          cancelButtonClassName='delete-account__delete-button'
          footerClassName='delete-account__footer'
          submitButtonClassName='delete-account__cancel-button'
          closeOnCancel={false}
        >
          <p className='delete-account-form__description'>
            <Trans i18nKey={`deleteAccount.${userRole}.confirmation.description`}>
              Are you sure you would like to delete your account on Oliv? Please note that this is irreversible and
              will <strong>permanently</strong> delete all of your profile data and job applications.
            </Trans>
          </p>
          { getDeleteAccountConnector() }
        </Modal>
      </div>
    )
  }
}

DeleteAccountSection.propTypes = {
  isDeleteAccountFormSubmitting: PropTypes.bool.isRequired,
  isDeleteAccountFormInvalid: PropTypes.bool.isRequired,
  submitDeleteAccountForm: PropTypes.func.isRequired,
  getDeleteAccountConnector: PropTypes.func.isRequired,
  userRole: PropTypes.string.isRequired
}

export default withTranslations(DeleteAccountSection)



