import React, { Component } from 'react'
import { Field } from 'redux-form'
import Divider from 'Shared/components/FormElements/Divider/index'
import { InputWithPreview } from 'Shared/components/FormElements/index'
import { PrimaryButton } from 'Shared/components/Button/index'
import withTranslations from 'Shared/hoc/withTranslations/index'

import './ChangePassword.scss'

class ChangePasswordForm extends Component {
  componentWillReceiveProps (nextProps) {
    nextProps.submitSucceeded && nextProps.reset()
  }

  render () {
    const { t, error, submitting, handleSubmit } = this.props
    return (
      <div className='change-password__container'>
        <Divider className='change-password__divider' />
        <h3>{t('common.changePassword')}</h3>
        <form className='change-password__form' onSubmit={handleSubmit(this.props.changePassword)}>
          <Field
            component={InputWithPreview}
            iconClassName='ion-md-lock'
            label={t('forms.oldPassword.label')}
            name='oldPassword'
            type='password'
          />
          <Field
            component={InputWithPreview}
            iconClassName='ion-md-lock'
            label={t('forms.newPassword.label')}
            placeholder={t('forms.newPassword.placeholder')}
            name='newPassword'
            type='password'
          />
          <Field
            component={InputWithPreview}
            iconClassName='ion-md-lock'
            label={t('forms.retypeNewPassword.label')}
            name='newPasswordRepeat'
            type='password'
          />
          <PrimaryButton
            className='change-password__submit'
            disabled={error || submitting}
            type='submit'>
            {submitting ? `${t('common.loading')}...` : t('common.changePassword')}
          </PrimaryButton>
        </form>
      </div>
    )
  }
}

export default withTranslations(ChangePasswordForm)



