import { reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import NotificationsForm from 'Candidate/forms/Settings/Notifications/NotificationsForm'
import { updateCandidate } from 'Shared/ducks/user/candidate'
import NotificationService from 'Shared/services/NotificationService'
import { t } from 'i18next'

export const formName = 'CandidateNotificationsForm'

let Form = reduxForm({
  form: formName,
  enableReinitialize: true,
  onSubmit: async (values, dispatch) => {
    try {
      await dispatch(updateCandidate(values))
      return NotificationService.success(t('alerts.formSuccessfullySaved'))
    } catch (e) {
      NotificationService.error(t('alerts.savingFormFailed'))
      return Promise.reject(e)
    }
  }
})(NotificationsForm)

const mapStateToProps = state => {
  const {
    profileCompletionNotificationEnabled,
    relevantjobOffersNotificationEnabled
  } = state.user.details

  return ({
    initialValues: {
      profileCompletionNotificationEnabled,
      relevantjobOffersNotificationEnabled
    }
  })
}

export default connect(mapStateToProps, null)(Form)



