import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'
import { pure } from 'recompose'
import withTranslations from 'Shared/hoc/withTranslations'
import { PageTopHeaderMobile } from 'Shared/components/PageTopHeader'
import { EmailAndPassword, Notifications } from './components'
import MembershipDetailsConnector from 'Candidate/connectors/MembershipDetails/MembershipDetailsConnector'
import './SettingsView.scss'
import { SwipeEntitiesListTab, SwipeEntitiesListWrapper } from 'Shared/components/SwipeEntitiesListTabs'

const settingsTabs = {
  membership: 'membership',
  emailAndPassword: 'emailAndPassword',
  notifications: 'notifications'
}

const CandidateSettingsViewMobile = ({ t, hasMembership, everHadOlivPlus }) => {
  const [activeTab, setActiveTab] = useState(hasMembership ? settingsTabs.membership : settingsTabs.emailAndPassword)

  const changeActiveTab = (event, activeTab) => setActiveTab(activeTab)

  const getContent = () => {
    switch (activeTab) {
      case settingsTabs.membership:
        return <MembershipDetailsConnector />
      case settingsTabs.emailAndPassword:
        return <EmailAndPassword />
      case settingsTabs.notifications:
        return <Notifications />
    }
  }

  return (
    <div>
      <PageTopHeaderMobile title={t('common.settings')} />
      <SwipeEntitiesListWrapper>
        {everHadOlivPlus && <SwipeEntitiesListTab
          text={t('candidate.settings.membershipDetails')}
          onClick={changeActiveTab}
          name={settingsTabs.membership}
          isActive={activeTab === settingsTabs.membership}
        />}
        <SwipeEntitiesListTab
          text={t('candidate.settings.emailAndPassword')}
          onClick={changeActiveTab}
          name={settingsTabs.emailAndPassword}
          isActive={activeTab === settingsTabs.emailAndPassword}
        />
        <SwipeEntitiesListTab
          text={t('candidate.settings.notifications')}
          onClick={changeActiveTab}
          name={settingsTabs.notifications}
          isActive={activeTab === settingsTabs.notifications}
        />
      </SwipeEntitiesListWrapper>
      {getContent()}
    </div>
  )
}

export default pure(withRouter(withTranslations(CandidateSettingsViewMobile)))



