import React, { useState, useEffect, Fragment } from 'react'
import 'Employer/components/MembershipDetails/MembershipDetails.scss'
import withTranslations from 'Shared/hoc/withTranslations'
import Divider from 'Shared/components/FormElements/Divider'
import Label from 'Shared/components/FormElements/Label'
import Modal from 'Shared/components/Modal'
import LocationContactConnector from 'Shared/connectors/LocationContact/LocationContactConnector'
import Notification from 'Shared/components/Notification/index'
import { Trans } from 'react-i18next'
import { formatDate } from 'Shared/helpers/date'
import { formats } from 'Shared/constants/Date'
import Tooltip from 'Shared/components/Tooltip/Tooltip'

const MembershipDetails = ({
  t,
  employerMessageAmountLeft,
  employerMessageMaxAmount,
  applicantBoostsAmountLeft,
  applicantBoostsMaxAmount,
  hasActiveSubscription,
  fetchActiveOlivPlusPayment,
  cancelOlivPlusPayment,
  paymentId,
  nextPaymentDate }) => {
  const [isModalVisible, setModalVisibility] = useState(false)
  const toggleCancelModal = () => setModalVisibility(!isModalVisible)

  useEffect(() => {
    fetchActiveOlivPlusPayment()
  }, [])

  const handleCancelSubscription = () => {
    cancelOlivPlusPayment(paymentId)
  }

  const inactiveText = <Trans i18nKey='candidate.settings.alerts.inactive' >
    Your membership is inactive. <a href='/candidate/oliv-plus/review' className='membership-details__alert-link'>Start a new membership</a> to continue using Oliv Plus.
  </Trans>

  return (
    <Fragment>
      {!hasActiveSubscription && <Notification iconClassName='icon-im-info-outline' messageText={inactiveText} warning />}
      <div className={'membership-details__container'}>
        <div>
          <div className='membership-details__title'>
            <h3>{t('employer.settings.membershipDetails')}</h3>
            {hasActiveSubscription && <div
              className='membership-details__cancel-button'
              onClick={toggleCancelModal}
            >
              {t('common.cancelSubscription')}
            </div>}
          </div>
          <div className='columns'>
            <div className='column'>
              <Label>{t('employer.settings.items')}</Label>
              <p className='membership-details__text'>{t('candidate.settings.applicationBoost')}</p>
              <p className='membership-details__text'>{t('candidate.settings.messagesToEmployer')}</p>
            </div>
            <div className='column'>
              <Label>{t('employer.settings.total')}</Label>
              <p className='membership-details__text'>{applicantBoostsMaxAmount}</p>
              <p className='membership-details__text'>{employerMessageMaxAmount}</p>
            </div>
            <div className='column'>
              <Label>{t('employer.settings.remaining')} <Tooltip trigger={'mouseenter'} content={t('candidate.settings.remainingTooltip')}>
                <i className='ion-md-information-circle remaining-items-tooltip' />
              </Tooltip>
              </Label>
              <p className='membership-details__text'>{applicantBoostsAmountLeft}</p>
              <p className='membership-details__text'>{employerMessageAmountLeft}</p>
            </div>
            <div>
              <Label>{t('candidate.settings.renewalDate')}</Label>
              <p className='membership-details__text'>{hasActiveSubscription ? formatDate(nextPaymentDate, formats.dashed) : '-'}</p>
              <p className='membership-details__text'>{hasActiveSubscription ? formatDate(nextPaymentDate, formats.dashed) : '-'}</p>
            </div>
          </div>
          <Divider className='membership-details__divider' />
          <div className='membership-details__section'>
            <div>
              {t('employer.settings.questions')}&nbsp;
              <LocationContactConnector />
            </div>
          </div>
          <Modal
            title={t('candidate.settings.cancelSubscriptionModal.title')}
            show={isModalVisible}
            handleClose={toggleCancelModal}
            handleCancel={toggleCancelModal}
            handleSubmit={handleCancelSubscription}
            cancelText={t('candidate.settings.cancelSubscriptionModal.cancelButton')}
            submitText={t('candidate.settings.cancelSubscriptionModal.submitButton')}
            submitButtonClassName={'membership-details__modal-submit-button'}
            closeOnSubmit
            showSubmit
            showCancel
          >
            <div className='membership-details__modal-description'>
              {t('candidate.settings.cancelSubscriptionModal.description')}
            </div>
          </Modal>
        </div>
      </div>
    </Fragment>
  )
}

export default withTranslations(MembershipDetails)



