import { connect } from 'react-redux'
import MembershipDetails from 'Candidate/views/SettingsView/components/MembershipDetails'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import {
  getApplicantBoostAmountLeft,
  getApplicantBoostMaxAmount,
  getEmployerMessageAmountLeft,
  getEmployerMessageMaxAmount,
  getOlivPlusExpirationDate,
  hasOlivPlusMembership
} from 'Shared/selectors/membership'
import { getOlivPlusTransactionId, hasActiveRecurringOlivPlusPayment, getOlivPlusNextPaymentDate } from 'Shared/selectors/transactions'
import { cancelOlivPlusPayment, fetchActiveReccuringCandidateTransaction } from 'Shared/ducks/transactions'

const mapStateToProps = state => ({
  applicantBoostsMaxAmount: getApplicantBoostMaxAmount(state),
  applicantBoostsAmountLeft: getApplicantBoostAmountLeft(state),
  employerMessageAmountLeft: getEmployerMessageAmountLeft(state),
  employerMessageMaxAmount: getEmployerMessageMaxAmount(state),
  membershipRenewalDate: getOlivPlusExpirationDate(state),
  hasActiveSubscription: hasActiveRecurringOlivPlusPayment(state),
  hasOlivPlusMembership: hasOlivPlusMembership(state),
  paymentId: getOlivPlusTransactionId(state),
  nextPaymentDate: getOlivPlusNextPaymentDate(state)
})

const mapDispatchToProps = dispatch => ({
  cancelOlivPlusPayment: id => dispatch(cancelOlivPlusPayment(id)),
  fetchActiveOlivPlusPayment: () => dispatch(fetchActiveReccuringCandidateTransaction())
})

export default withDeviceWidthDetector(connect(mapStateToProps, mapDispatchToProps)(MembershipDetails))



