import { connect } from 'react-redux'
import SettingsView from 'Candidate/views/SettingsView'
import {
  everHadOlivPlus,
  hasAnyBoostsLeft,
  hasAnyMessagesLeft,
  hasOlivPlusMembership
} from 'Shared/selectors/membership'

const mapStateToProps = state => ({
  hasMembership: hasOlivPlusMembership(state),
  hasAnyBoostsLeft: hasAnyBoostsLeft(state),
  hasAnyMessagesLeft: hasAnyMessagesLeft(state),
  everHadOlivPlus: everHadOlivPlus(state)
})

export default connect(mapStateToProps)(SettingsView)



