import React from 'react'
import { Switch, Route, Redirect, withRouter } from 'react-router-dom'
import { pure } from 'recompose'
import withTranslations from 'Shared/hoc/withTranslations'
import Settings from 'Shared/layouts/Settings/Settings'
import { EntitiesListTabWrapper, EntitiesListTabLink } from 'Shared/components/EntitiesListTabs'
import { PageTopHeaderMobile } from 'Shared/components/PageTopHeader'
import { EmailAndPassword, Notifications } from './components'
import MembershipDetailsConnector from 'Candidate/connectors/MembershipDetails/MembershipDetailsConnector'
import './SettingsView.scss'

const CandidateSettingsView = ({ t, everHadOlivPlus }) => {
  return (
    <Settings className='settings-view__container parsing--hidden'>
      <PageTopHeaderMobile title={t('common.settings')} />
      <EntitiesListTabWrapper>
        {everHadOlivPlus && <EntitiesListTabLink to='/candidate/settings/membership-details' text={t('candidate.settings.membershipDetails')} />}
        <EntitiesListTabLink to='/candidate/settings/email-and-password' text={t('candidate.settings.emailAndPassword')} />
        <EntitiesListTabLink to='/candidate/settings/notifications' text={t('candidate.settings.notifications')} />
      </EntitiesListTabWrapper>
      <Switch>
        <Route path='/candidate/settings/membership-details' component={MembershipDetailsConnector} />
        <Route path='/candidate/settings/email-and-password' component={EmailAndPassword} />
        <Route path='/candidate/settings/notifications' component={Notifications} />
        <Redirect from='/candidate/settings' to='/candidate/settings/email-and-password' />
      </Switch>
    </Settings>
  )
}

export default pure(withRouter(withTranslations(CandidateSettingsView)))



