/* eslint-disable no-throw-literal */
import isEmail from 'validator/lib/isEmail'
import APIErrorMessages from 'Shared/constants/APIErrorMessages'

const validate = values => {
  const errors = {}

  if (!values.newEmail) {
    errors.newEmail = {
      key: 'errors.requiredField',
      options: {
        field: 'New email'
      }
    }
  } else if (!isEmail(values.newEmail)) {
    errors.email = {
      key: 'errors.invalidEmail',
      options: {}
    }
  }

  return errors
}

const verifyNewEmail = verifyMethod => {
  return async (value) => {
    if (!value.newEmail) {
      throw { newEmail: {
        key: 'errors.emailNotProvided'
      } }
    }
    try {
      await verifyMethod(value.newEmail)
      return false
    } catch (e) {
      const { name, statusCode } = e.response.data.error
      switch (true) {
        case name === APIErrorMessages.EMAIL_ALREADY_TAKEN:
          throw { newEmail: {
            key: 'errors.emailAlreadyTaken'
          } }
        case name === APIErrorMessages.EMAIL_DOMAIN_BLACKLISTED:
          throw { newEmail: {
            key: 'errors.emailDomainBlackList'
          } }
        case statusCode === 400:
          throw { newEmail: {
            key: 'errors.emailAlreadyTaken'
          } }
        case statusCode === 422:
          throw { newEmail: {
            key: 'errors.emailAlreadyTaken'
          } }
        case statusCode === 403:
          throw { newEmail: {
            key: 'errors.emailDomainBlackList'
          } }
        default:
          throw { newEmail: {
            key: 'errors.emailNotProvided'
          } }
      }
    }
  }
}

export {
  validate,
  verifyNewEmail
}



