import React, { Component } from 'react'
import { Field } from 'redux-form'
import Divider from 'Shared/components/FormElements/Divider/index'
import Label from 'Shared/components/FormElements/Label'
import { InputWithIcon } from 'Shared/components/FormElements/index'
import { PrimaryButton } from 'Shared/components/Button/index'
import withTranslations from 'Shared/hoc/withTranslations/index'

import './ChangeEmail.scss'

class RequestChangeEmailForm extends Component {
  componentWillReceiveProps (nextProps) {
    nextProps.submitSucceeded && nextProps.reset()
  }

  render () {
    const { t, error, submitting, handleSubmit, userEmail } = this.props
    return (
      <div className='change-email__container'>
        <Divider className='change-email__divider' />
        <h3>{t('common.changeEmailAddress')}</h3>
        <form className='change-email__form' onSubmit={handleSubmit(this.props.requestChangeEmail)}>
          <div>
            <div className='change-email__current-email-field'>
              <Label>{t('forms.currentEmailAddress.label')}</Label>
              <div className='change-email__current-email'>{userEmail}</div>
            </div>
          </div>
          <Field
            component={InputWithIcon}
            iconClassName='ion-md-mail'
            label={t('forms.newEmailAddress.label')}
            placeholder={t('forms.newEmailAddress.placeholder')}
            name='newEmail'
            type='email'
          />
          <PrimaryButton
            className='change-email__submit'
            disabled={error || submitting}
            type='submit'>
            {submitting ? `${t('common.loading')}...` : t('common.changeEmailAddress')}
          </PrimaryButton>
        </form>
      </div>
    )
  }
}

export default withTranslations(RequestChangeEmailForm)



