import React from 'react'
import MembershipDetails from './MembershipDetails'
import MembershipDetailsMobile from './MembershipDetails.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const MembershipDetailsComponent = props => props.isMobile ? <MembershipDetailsMobile {...props} /> : <MembershipDetails {...props} />

export default withDeviceWidthDetector(MembershipDetailsComponent)



