import React, { PureComponent } from 'react'
import withTranslations from 'Shared/hoc/withTranslations/index'
import ConfirmModal from 'Shared/components/DeleteModal/ConfirmModal'
import './ExportDataSection.scss'

class ExportDataSection extends PureComponent {
  state = {
    isSuccessModalOpen: false
  }

  openSuccessModal = () => this.setState({ isSuccessModalOpen: true })
  closeSuccessModal = () => this.setState({ isSuccessModalOpen: false })

  render () {
    const { t, exportData } = this.props

    return (
      <div>
        <div>
          <p className='export-data__description'>
            {`${t('manageAccount.exportData.description')} `}
            <button id='exportData' onClick={exportData(this.openSuccessModal)} className='export-data__button'>{t('manageAccount.exportData.exportData')}</button>
          </p>
        </div>
        <ConfirmModal
          title={t('manageAccount.exportData.exportDataSuccess.title')}
          content={t('manageAccount.exportData.exportDataSuccess.description')}
          handleConfirm={this.closeSuccessModal}
          submitText={t('common.ok')}
          isModalVisible={this.state.isSuccessModalOpen}
          hideConfirmModal={this.closeSuccessModal}
          showCancel={false}
        />
      </div>
    )
  }
}

export default withTranslations(ExportDataSection)



