import React, { PureComponent } from 'react'
import CandidateChangePasswordFormConnector from 'Candidate/connectors/Forms/Settings/CandidateChangePasswordFormConnector'
import CandidateRequestChangeEmailConnector from 'Candidate/connectors/Forms/Settings/CandidateRequestChangeEmailConnector'
import DeleteAccountSectionConnector from 'Candidate/connectors/DeleteAccountSection/DeleteAccountSectionConnector'
import Divider from 'Shared/components/FormElements/Divider/index'
import CandidateExportDataConnector from 'Candidate/connectors/ExportData/CandidateExportDataConnector'
import withTranslations from 'Shared/hoc/withTranslations'

import '../SettingsView.scss'
import { Helmet } from 'react-helmet'

class EmailAndPassword extends PureComponent {
  render () {
    const { t } = this.props

    return (
      <div>
        <Helmet>
          <title>{t('meta.candidate.title.personalDetails')}</title>
        </Helmet>
        <CandidateChangePasswordFormConnector />
        <CandidateRequestChangeEmailConnector />
        <div className='settings-view__section'>
          <h3>{t('manageAccount.manageYourData')}</h3>
          <Divider className='settings-view__divider' />
          <CandidateExportDataConnector />
          <DeleteAccountSectionConnector />
        </div>
      </div>
    )
  }
}

export default withTranslations(EmailAndPassword)



