import { reduxForm } from 'redux-form'
import { t } from 'i18next'
import DeleteAccountForm from 'Shared/forms/DeleteAccount/DeleteAccountForm'
import NotificationService from 'Shared/services/NotificationService'
import validate from './CandidateDeleteAccountValidator'
import UserService from 'Shared/services/UserService'
import { logout } from 'Shared/ducks/user'

const userService = new UserService()

export const formName = 'CandidateDeleteAccountForm'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmit: async (values, dispatch) => {
    try {
      await userService.deleteCandidateAccount()
      dispatch(logout())
      return NotificationService.success(t('alerts.deleteAccountSuccess'))
    } catch (e) {
      return NotificationService.error(t('alerts.deleteAccountFailed'))
    }
  }
})(DeleteAccountForm)

export default Form



