import { PASSWORD_LENGTH } from 'Shared/constants/Validation'

export default values => {
  const errors = {}
  if (!values.newPassword) {
    errors.newPassword = {
      key: 'errors.requiredField',
      options: {
        field: 'New password'
      }
    }
  }

  if (!values.oldPassword) {
    errors.oldPassword = {
      key: 'errors.requiredField',
      options: {
        field: 'Current password'
      }
    }
  }

  if (values.newPassword && values.newPassword.length < PASSWORD_LENGTH) {
    errors.newPassword = {
      key: 'errors.passwordTooShort',
      options: {
        chars: PASSWORD_LENGTH
      }
    }
  }

  if (!values.newPasswordRepeat) {
    errors.newPasswordRepeat = {
      key: 'errors.requiredField',
      options: {
        field: 'Repeat new password'
      }
    }
  }

  if (values.newPasswordRepeat !== values.newPassword) {
    errors.newPasswordRepeat = {
      key: 'errors.repeatPassword'
    }
  }

  return errors
}



