import { reduxForm, SubmissionError } from 'redux-form'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { t } from 'i18next'
import ChangePasswordForm from 'Shared/forms/ChangePassword/ChangePasswordForm'
import validate from 'Shared/validators/changePassword'
import NotificationService from 'Shared/services/NotificationService'
import { INVALID_PASSWORD } from 'Shared/constants/ErrorCodes'
import UserService from 'Shared/services/UserService'

const userService = new UserService()

export const formName = 'CandidateChangePasswordForm'

let Form = reduxForm({
  form: formName,
  validate
})(ChangePasswordForm)

const mapDispatchToProps = dispatch => ({
  changePassword: async (values) => {
    try {
      await userService.changeCandidatePassword(values.oldPassword, values.newPassword)
      return NotificationService.success(t('alerts.resetPasswordSuccess'))
    } catch (err) {
      if (err.response.data.error.code === INVALID_PASSWORD) {
        throw new SubmissionError({ oldPassword: { key: 'errors.wrongCurrentPassword' } })
      }
      NotificationService.error(t('alerts.resetPasswordFailed'))
      throw err
    }
  }
})

export default withRouter(connect(null, mapDispatchToProps)(Form))



